#help-menu-wrapper {
  z-index: 1;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.5);
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#help-menu {
  background-color: var(--black);
  color: var(--white);
  height: auto;
  width: 75vw;
  overflow: auto;
  padding: 40px;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#help-menu button {
  width: 10%;
  height: 60px;
}