#accessibilityBar {
    width: 100%;
    min-height: 6vh;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: sticky;
    top: 0;
    z-index: 999;
}
#visibleMenu {
    box-sizing: border-box;
    list-style-type: none;
    display: flex;
    min-height: 6vh;
    justify-content: flex-end;
    align-items: center;
}
#screenreaderArea {
    margin: 5px;
    align-content: center;
    border-radius: 5px;
    text-align: center;
    padding: 5px;
}
#areaText {
    display: inline-block;
    margin-left: 2vh;
    margin-right: 2vh;
    align-content: center;
}
#screenreaderButton {
    margin: 10px;
}
#hideButton{
    margin: 2vh 2vh;
}
#colorText {
    display: inline-block;
    align-content: center;
    margin: 5px 2vh 0;
}
#colorItem {
    display: inline-block;
    margin: 0 5px;
    padding: 3px;
}
#passColor{
    margin: 0 10px;
    border: solid black 2px;
    display: inline-block;
    height: 30px;
    width: 30px;
    vertical-align: middle;
}
#failColor{
    margin: 0 10px;
    border: solid black 2px;
    display: inline-block;
    height: 30px;
    width: 30px;
    vertical-align: middle;
}
#exampleColors{
    align-content: center;
    border: solid black 2px;
    margin: 5px;
}