:root {
  --black: #000;
  --white: #FFF;
  --light-black: #3C3C39;
  --grey: #B8B8B2;
  --hover-grey: hsl(60, 4%, 55%);
  --green: #00E585;
  --red: #E50000;
}

#tester-instance {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 30px;
  min-height: 100%;
}

/* Top Section Styles */

#top-section {
  margin: 0 auto;
  flex-direction: column;
  display: flex;
  align-items: center;
  gap: 10px;
  width: 60vw;
}

#title {
  text-align: center;
  font-size: 32pt;
  width: max-content;
}

#directory-form {
  display: grid;
  flex-direction: row;
  gap: 0 3%;
  justify-content: center;
  grid-template-columns: 6fr 1fr 1fr 1fr;
  font-size: 16pt;
}

.tester-action-button {
  border-radius: 10px;
  font-weight: 700;
  font-size: inherit;
  width: max-content;
  padding: 5px 10px;
}

#directory-submit, #run-tests-button, #help-button {
  background-color: var(--grey);
  color: var(--black);
  border: none;
}

#directory-submit:hover, #run-tests-button:hover, #help-button:hover {
  background-color: var(--hover-grey);
  cursor: pointer;
}

#directory-input {
  background-color: var(--light-black);
  color: var(--grey);
  border: none;
  padding: 5px 10px;
  border-radius: 10px;
  width: 100%;
  grid-column: 1 / span 1;
  font-size: inherit;
  text-overflow: ellipsis;
}

#directory-submit {
  grid-column: 2 / span 1;
}

#run-tests-button {
  grid-column: 3 / span 1;
}

#help-button {
  grid-column: 4 / span 1;
}

/* Test Component Styles */

#test-file-list {
  width: 100%;
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  gap: 20px;
}

.test-file {
  background-color: var(--light-black);
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 12fr 2fr;
  border-radius: 20px;
  padding: 20px;
  width: 65vw;
}

.test-file-heading {
  font-size: 36pt;
  grid-row: 1 / span 2;
  grid-column: 1 /span 1;
}

.test-file-title {
  font-weight: 500;
}

.test-file-result {
  font-family: monospace;
  font-weight: 300;
}

.test-result-icon {
  grid-row: 1 / span 2;
  grid-column: 2 /span 1;
  margin: auto;
  height: 80px;
  width: 80px;
}

.test-result-icon[data-test-result="passed"] {
  color: var(--green);
}

.test-result-icon[data-test-result="failed"] {
  color: var(--red);
}

.test-result-icon[data-test-result=""] {
  color: var(--white);
}