#test-modal-wrapper {
  z-index: 1;
  position: fixed;
  background-color: rgba(255, 255, 255, 0.5);
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#test-modal {
  height: 75vh;
  width: 75vw;
  background-color: var(--black);
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  border-radius: 40px;
  overflow: auto;
}

#test-modal > h2 {
  font-size: 40pt;
  text-align: center;
}

.modal-result {
  font-family: monospace;
  font-weight: 300;
}

.test-list {
  padding: 0;
  list-style-type: none;
  width: 75%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.test-name {
  font-style: italic;
  font-size: 20pt;
  text-align: center;
}

.diffs-list {
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.diffs-list-label {
  display: flex;
}

.diffs-list-label > h4 {
  text-align: center;
  width: 100%;
  font-weight: 300;
}

.diff {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  padding: 5px;
}

.diff[data-diff-type="INSERTION"], .diff[data-diff-type="DELETION"] {
  background-color: rgba(255, 0, 0, 0.3);
}

.diff[data-diff-type="UNCHANGED"] {
  background-color: rgba(0, 128, 0, 0.3);
}

.diff-col {
  display: flex;
  gap: 10px;
}

.diff-line-number::after {
  content: ".";
}

#close-modal-button {
  height: 10%;
  width: 20%;
  font-size: 16pt;
  margin-bottom: 10px;
}