/**
NORMAL DARK MODE
 */
.normalDark {
    color: #FFFFFF;
    background-color: black;
}
/*
ACCESSIBILITY BAR COLORS
 */
.normalDark #screenreaderArea{
    border: solid #f8f4e3 2px;
}
.normalDark #accessibilityBar{
    background-color: #3c3c39;
}
.normalDark .aws-btn {
    --button-primary-color: #475472;
    --button-primary-color-dark: #2a3143;
    --button-primary-color-light: #d4d9e4;
    --button-primary-color-hover: #424e6a;
    --button-primary-border: none;

    --button-secondary-color: #fffc6c;
    --button-secondary-color-dark: #b9b500;
    --button-secondary-color-light: #6c6a00;
    --button-secondary-color-hover: #fffb3e;
    --button-secondary-border: none;
}
.normalDark #passColor{
    background-color: #00E585;
}
.normalDark #failColor{
    background-color: #E50000;
}
/*
TEST INSTANCE COLORS
 */
.normalDark #directory-input{
    background-color: #3c3c39;
    color: #B8B8B2;
}
.normalDark .test-file{
    background-color: #3C3C39;
}
/* ICON COLORING */
.normalDark .test-result-icon[data-test-result="passed"] {
    color: #00E585;
}
.normalDark .test-result-icon[data-test-result="failed"] {
    color: #E50000;
}
.normalDark .test-result-icon[data-test-result=""] {
    color: #FFFFFF;
}
/*
MOREINFO MODAL STYLING
 */
.normalDark #test-modal-wrapper{
    background-color: rgba(255, 255, 255, 0.5);
}
.normalDark #test-modal {
    background-color: black;
    color: white;
}
.normalDark .diff[data-diff-type="INSERTION"], .normalDark .diff[data-diff-type="DELETION"] {
    background-color: rgba(255, 0, 0, 0.3);
}
.normalDark .diff[data-diff-type="UNCHANGED"] {
    background-color: rgba(0, 128, 0, 0.3);
}

